/*Headers*/
body {
  -webkit-font-smoothing: antialiased;
  text-rendering: optimizeLegibility;
  font-family: "Overpass", sans-serif;
  letter-spacing: 0px;
  font-size: 17px;
  color: #8d8f90;
  font-weight: 400;
  line-height: 32px;
  background-color: #edefef;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  color: #25292a;
  margin: 0px 0px 10px 0px;
  font-family: "Overpass", sans-serif;
  font-weight: 700;
  letter-spacing: -1px;
  line-height: 1;
}


.page-header {
  background: url(https://cdn.britannica.com/83/189283-050-DF7E0E75/Banknotes-world.jpg)
    no-repeat;
  position: relative;
  background-size: cover;
}
.page-caption {
  padding-top: 80px;
  padding-bottom: 100px;
}
.page-title {
  font-size: 5em;
  line-height: 1;
  color: rgb(0,15,118);
  font-weight: 600;
  text-align: center;
  font-family: "Dancing Script", cursive;
  margin-bottom: 1em;
  text-shadow: 5px 5px 0px #00d0ff;
  background-color: rgba(255,255,255,0.6);
  border-radius: 50px;
}

.card-section {
  position: relative;
  bottom: 60px;
}
.card-block {
  padding: 80px;
  border-radius: 20px;
  box-shadow: 5px 5px 20px rgb(0 0 0 / 45%);
}
.section-title {
  margin-bottom: 60px;
}

.cards {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 1rem;
  gap: 1rem;
}

/*Cambiar btn*/
.botonChange{
    border-radius: 25px !important;
    padding: 0 !important;
}

.imgChange{
  height: 30px;
}

/*Inicio Input Importe*/
.form__group {
  position: relative;
  padding: 15px 0;
  width: 90%;
}

.form__field {
  font-family: inherit;
  width: 100%;
  border: 0;
  border-bottom: 2px solid rgb(0,15,118);
  outline: 0;
  color: rgb(0,15,118);
  padding: 7px 0;
  background: transparent;
  transition: border-color 0.2s;
}
.form__field::-webkit-input-placeholder {
  color: transparent;
}
.form__field:-ms-input-placeholder {
  color: transparent;
}
.form__field::placeholder {
  color: transparent;
}
.form__field:-ms-input-placeholder ~ .form__label {
  cursor: text;
  top: 25px;
}
.form__field:placeholder-shown ~ .form__label {
  cursor: text;
  top: 25px;
}

.form__label {
  position: absolute;
  top: 0;
  display: block;
  transition: 0.2s;
  font-size: 1rem;
  color: rgb(0,15,118);
}

.form__field:focus {
  padding-bottom: 6px;
  font-weight: 700;
  border-width: 3px;
  border-image: linear-gradient(to right, rgb(0, 101, 196), rgb(156, 197, 207));
  border-image-slice: 1;
}
.form__field:focus ~ .form__label {
  position: absolute;
  top: 0;
  display: block;
  transition: 0.2s;
  font-size: 1rem;
  color: rgb(0, 101, 196);
  font-weight: 700;
}

.form__field:required,
.form__field:invalid {
  box-shadow: none;
}

