}

/*General*/

html {
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*,
*::after,
*::before {
  box-sizing: inherit;
}

::-webkit-scrollbar {
  width: 10px;
}

::-webkit-scrollbar-track {
  background-color: rgb(255, 255, 255);
}

::-webkit-scrollbar-thumb {
  background: rgb(4, 19, 151);
  background: linear-gradient(
    176deg,
    rgba(4,19,151, 1) 0%,
    rgb(56, 146, 219) 67%,
    rgb(130, 230, 255) 100%
  );
}
.navMio {
  margin-bottom: 0%;
  padding-bottom: 0%;
}

.logo {
  font-size: large;
}

.titulo {
  width: 100%;
  height: 25%;
  text-align: center;
  font-size: x-large;
}
.icono {
  width: 50px;
  height: 50px;
}
.contenedor {
  background-color: rgb(173, 173, 173);
  box-sizing: border-box;
  width: 60%;
  margin: auto auto auto auto;
  padding-top: 2%;
  padding-bottom: 2%;
}

.inp {
  width: 100%;
}
.cuadro {
  border-color: rgb(206, 206, 206);
  border-style: solid;
  background: white;
  border-width: thin;
}
.cosa {
  width: 100%;
  height: 25%;
  text-align: center;
  margin-top: 10%;
  margin-bottom: 2%;
}

.btm{
  float: right;
}

@media (min-width: 370px) and (max-width: 640px) {
  .contenedor {
    width: 100%;
  }
  .logo {
    font-size: 12px;
  }
}
@media (max-width: 370px) {
  .logo {
    font-size: 10px;
  }
}

